.hotel-details {
    width: 415px;
    height: 735px;
    padding-top: 22px;
    padding-bottom: 40px;
    background-image: linear-gradient(rgba(0, 0, 0, 0.65), rgba(0, 0, 0, 0.65)), url(/src/images/main/main-bg-hotel-mob.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-color: black;
    position: relative;

    @include retina {
        background-image: linear-gradient(rgba(0, 0, 0, 0.65), rgba(0, 0, 0, 0.65)), url(/src/images/main/main-bg-hotel-mob-2x.jpg);
    }

        @media screen and (min-width: 1024px) {
        height: 620px;
        width: 1024px;
        background-image: linear-gradient(rgba(0, 0, 0, 0.65), rgba(0, 0, 0, 0.65)), url(/src/images/main/main-bg-hotel-tab.jpg);

        @include retina {
            background-image: linear-gradient(rgba(0, 0, 0, 0.65), rgba(0, 0, 0, 0.65)), url(/src/images/main/main-bg-hotel-tab\ -2x.jpg);
        }
    }

        @media screen and (min-width: 1280px) {
            width: 1280px;
        background-image: linear-gradient(rgba(0, 0, 0, 0.65), rgba(0, 0, 0, 0.65)), url(/src/images/main/main-bg-hotel-desk.jpg);

        @include retina {
            background-image: linear-gradient(rgba(0, 0, 0, 0.65), rgba(0, 0, 0, 0.65)), url(/src/images/main/main-bg-hotel-desk-2x.jpg);
        }
    } 
}



.hotel-details-logo {
    font-family: $fontFilm;
    font-weight: 700;
    font-size: 40px;
    line-height: 1.075;
    letter-spacing: 0.3em;
    color: #FFFFFF;
    text-transform: uppercase;  
    margin-bottom:12px;
    display: flex;
    margin-left: 144px;

    @media screen and (min-width: 1024px){
        font-size: 64px;
        line-height: 1.078;
        margin-bottom: 16px;
        margin-left: 544px;
    }
@media screen and (min-width: 1280px){
    margin-left: 740px;
}


        
    
}

.hotel-details-title{
font-family: $fontMontserrat;
font-weight: 700;
font-size: 20px;
line-height: 1.2;
letter-spacing: 0.02em;
color: #FFFFFF;
margin-bottom: 20px;
display: flex;
    margin-left: 201px;

@media screen and (min-width: 1024px) {
    font-size: 24px;
    line-height: 1.16;
    margin-bottom: 8px;
    margin-left: 659px;
}
@media screen and (min-width: 1280px){
    margin-left: 855px;
}

}



.hotel-details-descr{
    font-family: $fontMontserrat;
    font-weight: 600;
    font-size: 12px;
    line-height: 1.25;
    letter-spacing: 0.02em;
    color: #BABABA;
    margin-bottom: 48px;
    margin-left: 31px;
    max-width: 243px;

    @media screen and (min-width: 1024px){
        max-width: 549px;
        font-size: 16px;
        margin-bottom: 40px;
        margin-left: 197px;
    }


        @media screen and (min-width: 1280px) {
            max-width: 595px;
            margin-left: 347px;
        }

}

.details-hotel-btn{
    margin-bottom: 16px;
}

.details-hotel-link{
   display: flex;
   height: 48px;
   justify-content: center;
   align-items: center;
}


.btns-box{
display: flex;
max-width: 236px;
flex-direction: column;
position: absolute;
bottom: 40px;
right: 89px;


    @media screen and (min-width: 1024px) {
            bottom: 100px;
