.about-hotel-section {
    margin-top: 18px;
    position: relative;

    @include tablet {
        padding-top: 120px;
        margin-top: 0;
        padding-bottom: 100px;
    }

    @include desktop {}
}

.visually-hidden {
    position: absolute;
    white-space: nowrap;
    width: 1px;
    height: 1px;
    overflow: hidden;
    border: 0;
    padding: 0;
    clip: rect(0 0 0 0);
    clip-path: inset(50%);
    margin: -1px;
}

.about-hotel-text {
    text-align: right;
    font-family: $fontTimesNewRoman;
    font-weight: 400;
    font-size: 20px;
    line-height: calc(23/20);
    color: $preTitleColorGray;

    @include tablet {
        display: block;
        font-size: 32px;
        line-height: calc(37/32);
    }

    @include desktop {}
}

.about-hotel-logo {
    font-family: $fontFilm;
    font-weight: 700;
    font-size: 40px;
    line-height: calc(43/40);
    letter-spacing: 0.3em;
    color: $titleColorDarkGrey;
    text-align: right;
    margin-top: 6px;

    @include tablet {
        font-size: 64px;
        line-height: calc(69/64);
    }

    @include desktop {}
}

.about-hotel-block-descript {
    margin-top: 16px;
    margin-left: auto;
    margin-right: 0;
    max-width: 242px;

