.about-hotel-slick{
   
        margin-left: -25%;
        width: 330px;
        
    
        @include tablet {
            margin-top: -10%;
            width: 428px;
            margin-bottom: 0;
            margin-left: 45px;
            height: 194px;
        }
    
        @include desktop {
            width: 548px;
            margin-left: 0px;
        }
}



.slick-prev:before,
.slick-next:before {
    content:'';
    background: url(/src/images/svg/arrow-right.svg) 0 0 /100% no-repeat;
    width: 32px;
    height: 32px;
}

.slick-prev {
   
    background: url(/src/images/svg/arrow-right.svg) 0 0 /100% no-repeat;
    width: 32px;
    height: 32px;
    top: 185px;
    left: 107px;
    
    @include tablet {
            top: 225px;
            left: 0px;
    }
    
    @include desktop {
            top: 285px;
            left: 0px;
            margin-bottom: 50px;
    }
}

.slick-next {
    background: url(/src/images/svg/arrow-left.svg) 0 0 /100% no-repeat;
    width: 32px;
    height: 32px;
    top: 185px;
    left: 165px;
    @include tablet {
        top: 225px;
        left: 64px;
    }

    @include desktop {
        left: 66px;
        top: 285px;
    }
}


.slick-next:hover,
.slick-next:focus {
    background: url(/src/images/svg/arrow-left.svg) 0 0 /100% no-repeat;
    width: 32px;
    height: 32px;
    
}

.slick-prev:hover,
.slick-prev:focus{
   
        background: url(/src/images/svg/arrow-right.svg) 0 0 /100% no-repeat;
        width: 32px;
        height: 32px;
}

